<template>
<div>
	<p>Home page</p>

	<form v-on:submit.prevent="submit">

		URL: <input type="text" v-model="formData.url"></br>
		cssSelecotr: <input type="text" v-model="formData.cssSelector">
		<button v-on:click="addCss()" class="btn btn-primary btn-sm">追加</button>
		</br>

		<span>cssSelecotr: {{formData.cssSelector}}</span></br>

		</br></br><span>URL: {{formData.url}}</span>
		</br><span>cssSelectors: {{formData.css}}</span>
		<p>で正しいですか？</p>
		<input type="submit" value="submit">

	</form>
</div>
</template>

<script>
import axios from 'axios'

export default {
	delimiters: ['[[', ']]'],
	data() {
		return {
			results: [],
			formData: {
				'url': '',
				'cssSelector': '',
				'css': []
			}
		}
	},
	methods: {
		submit: function() {
			console.log(this.formData)
		},
		addCss: function() {
			var newCss = this.formData.cssSelector;
			this.formData.css.push(newCss	);
			this.formData.cssSelector = '';
		},
	}

}
</script>



